import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },

  minRatio: {
    displayName: 'minRatio',
    formConfig: {
      overwriteRule: true,
      rule: [
        v => /^(-)?[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'please enter the correct min Ratio',
      ],
    },
  },

  maxRatio: {
    displayName: 'maxRatio',
    formConfig: {
      overwriteRule: true,
      rule: [
        v => /^(-)?[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'please enter the correct max Ratio',
      ],
    },
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: true,
    },
  },

}

const config = {

  add: function (item) {
    console.log('add', item)

    if (item.minRatio > item.maxRatio) {
      [item.maxRatio, item.minRatio] = [item.minRatio, item.maxRatio]
    }
    if (item.minRatio <= 0) {
      item.minRatio = 'null'
    }

    if (item.maxRatio > 1) {
      item.maxRatio = 'null'
    }

    return hillo.postWithUploadFile('DCache.php?op=createDCacheColor', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    if (item.minRatio > item.maxRatio) {
      [item.maxRatio, item.minRatio] = [item.minRatio, item.maxRatio]
    }
    if (item.minRatio <= 0) {
      item.minRatio = 'null'
    }

    if (item.maxRatio > 1) {
      item.maxRatio = 'null'
    }

    return hillo.postWithUploadFile('DCache.php?op=updateDCacheColor', item, {
      showLoading: true,
    })
  },

  remove: function (id) {
    return hillo.post('DCache.php?op=deleteDCacheColor', { id: id })
  },

  load: async function (filter) {
    const tempItem = (await hillo.get('DCache.php', {
      op: 'showDCacheColor',
      ...filter,
    }))
      .content

    console.log('load tempItem before', tempItem)
    for (const i in tempItem) {
      console.log('show tempItem i', tempItem[i])
      if (tempItem[i].minRatio === null) tempItem[i].minRatio = '- min'
      if (tempItem[i].maxRatio === null) tempItem[i].maxRatio = '+ max'
    }
    console.log('load tempItem after', tempItem)
    return tempItem
  },

}

export default IKDataEntity.ModelFactory(entity, config)
